import styled from "@emotion/styled";
import React, { useEffect } from "react";
import MIDIVal, {MIDIValInput} from "@midival/core";

const Canvas = styled.canvas`
    width: 100%;
    height: 100%;
    background: black;
`;

const drawTri = (ctx: CanvasRenderingContext2D, {x, y}, {i,j}, size, ang, color: number[], highlightRegions: number[]) => {
    
    const isHighlight = highlightRegions.map(reg => Math.abs(i+j - reg+56) < 1).includes(true);

    if (isHighlight) {
        color = color.map(x => x + 50);
    }
    ctx.fillStyle = `rgb(${color.join(',')})`;

    
    ctx.beginPath();
    ctx.moveTo(x, y);
    const rad = ang * Math.PI / 180;
    ctx.lineTo(x + size, y);
    ctx.lineTo(x + size / 2, y - size * Math.sin(rad));
    ctx.lineTo(x, y);
    ctx.stroke();
    ctx.fill();
}

let notesOn = [];

const bindMidival = () => {
    console.log(MIDIVal);
    MIDIVal.connect().then(() => {
        MIDIVal.onDeviceConnected(device => {
            console.log("DEVICE", device);
            if (device.input) {
                const inp = new MIDIValInput(device);
                inp.onAllNoteOn((key) => {
                    notesOn.push(key);
                });

                inp.onAllNoteOff((key) => {
                    notesOn = notesOn.filter(x => x !== key);
                })
            }
        });
    });
}

const initOnCanvas = () => {
    render(Date.now());
    requestAnimationFrame(initOnCanvas);
}

const render = (t) => {
    const canv = document.querySelector('canvas');
    const ctx = canv.getContext("2d");
    const canvas = document.querySelector("canvas"); // hacky
    canvas.width = window.innerWidth;
    canvas.height = window.innerWidth / 2;
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    const S = 100 + Math.sin(t / 7314) * 30;
    const ang = 60 + Math.sin(t / 3452) * 10;
    const rad = ang * Math.PI / 180;
    const diff = Math.sin(t/10000) * S / 2;
    const diff2 = Math.cos(t/2500) * S / 4;


    ctx.strokeStyle = "#333";
    const c = Math.max(0, 20 + Math.sin(t/1000) * 30);

    const color = [c+60*Math.sin(t/4223),c, c+20*Math.cos(t/5132)];
    ctx.lineWidth = 2;

    for(let j=0;j<50;j++) {
        for(let i=-25;i<25;i++) {
            drawTri(ctx, {x: 100+S*i+S/2 * j, y: 50+(S+2)*j*Math.sin(rad)-diff2}, {i,j}, S, ang, color, notesOn/*[40]*/);
        }
    }
}

export const HeroAlt = ({}) => {
    useEffect(() => {
        try {
            bindMidival();
        } catch (e) {
            console.log("MIDI not available");
        }
        initOnCanvas();
    }, []);
    return <Canvas />
}